import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './main/main.component';
import { LoginComponent } from './login/login.component';
import { HomeComponent } from './home/home.component';
import { ErrorComponent } from './error/error.component';
import { CategoryComponent } from './category/category.component';
import { ListShoesComponent } from './list-shoes/list-shoes.component';
import { LogoutComponent } from './logout/logout.component';
import { RouteGuardService } from './service/route-guard.service';
import { AddShoesComponent } from './add-shoes/add-shoes.component';
import { ShoesDetailsComponent } from './shoes-details/shoes-details.component';
import { CartComponent } from './cart/cart.component';

const routes: Routes = [
  { path:'', component: MainComponent},
  { path:'main', component: MainComponent},
  { path: 'category/:category', component: CategoryComponent},
  { path: 'shoesDetail/:key', component: ShoesDetailsComponent},
  { path: 'cart', component: CartComponent},
  { path:'login', component: LoginComponent},
  { path:'home/:key', component: HomeComponent, canActivate:[RouteGuardService]},
  { path:'addShoes', component: AddShoesComponent, canActivate:[RouteGuardService]},
  { path:'listShoes/:category', component: ListShoesComponent, canActivate:[RouteGuardService]},
  { path:'logout', component: LogoutComponent, canActivate:[RouteGuardService]},

  { path:'**', component: ErrorComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
